<template>
    <van-notice-bar
            left-icon="volume-o"
            text=""
    />
    <van-loading v-if="Loading" size="24px" color="#0094ff" vertical>...</van-loading>
    <div v-else-if="packingOrder.length===0 && !Loading" style="height: 100%;text-align: center;line-height: 400px;font-size: 30px;font-weight: bold"></div>
    <div v-else>
        <van-cell  v-for="item of packingOrder" v-bind:key="item" :title=item.customer  is-link value="" @click="changeStatus(item)" />
    </div>
</template>

<script>
    import axios from 'axios'
    import {Dialog} from 'vant'
    import {Toast} from 'vant'
    import router from '../router'
    export default {
        name: "pack-status-change",
        data(){
            return{
                packingOrder:[],
                Loading:true,
            }
        },
        created(){
            axios.get(this.$store.state.domain+"order_get_by_keyword",{
                params:{
                    keyword:"packing"
                }
            }).then((res)=>{
                console.log(res)
                this.packingOrder = res.data
                this.Loading = false
            })
        },
        methods:{
            changeStatus(item){
                Dialog.confirm({
                    title: '',
                    message: '',
                })
                    .then(() => {
                        // on confirm
                        axios.get(this.$store.state.domain+'order_get_by_id_set_status',{
                            params:{
                                orderID:item.id
                            }
                        }).then((res)=>{
                            if (res.status===200){
                                Toast.success('')
                                router.go(0)
                            }else {
                                Toast.fail(res.data)
                            }
                        })

                    })
                    .catch(() => {
                        // on cancel
                        Toast('')
                    });
            }
        }
    }
</script>

