<template>
    <van-notice-bar
            left-icon="volume-o"
            text=""
    />
    <van-checkbox-group @change="products_checked(checked)" style="margin: 10px 30px;" v-model="checked" direction="horizontal">
        <van-checkbox  v-for="index of listProduct" v-bind:key="index" :name="index.name">{{index.name}}</van-checkbox>
    </van-checkbox-group>

    <orderadditem  v-for="product of products"
                   v-bind:key="product"
                   :itemName="product.name"
                   :detailData="product.stock"
                   :itemId="product.id"></orderadditem>

    <van-cell-group>
        <van-field v-model="customerName" label="" placeholder="" />
    </van-cell-group>
    <van-cell-group>
        <van-field v-model="destination" label="" placeholder="" />
    </van-cell-group>
    <van-field
            v-model="remark"
            rows="2"
            autosize
            label=""
            type="textarea"
            maxlength="50"
            placeholder=""
            show-word-limit
    />
    <van-button :loading="adding_order" :disabled="adding_order" loading-text="..."  size="large" type="success" @click="order_add()"></van-button>
</template>
<script>
    import orderadditem from '@/components/OrderAddItem'
    import router from "../router"
    import { ref } from 'vue'
    import axios from 'axios'
    import {Toast} from "vant"

    export default {
        name: "order-add",
        components:{
            orderadditem
        },
        setup() {
            const checked = ref([])
            return { checked }
        },
        created(){
            axios.get(this.$store.state.domain+'product_get',{}).then((res)=>{
                this.listProduct=res.data;
            })
        },
        data(){
            return{
                customerName:"",
                destination:"",
                remark:"",
                //
                products:[],
                product:0,
                lastItems:[],
                // checked:[]
                //products
                listProduct:[],
                //products
                showProduct:[],
                orderProduct:[],
                adding_order:false
            }
        },
        methods:{
            products_checked(checked){
                if (this.checked.length===0){
                    this.products = this.showProduct
                    this.$store.commit("orderProductItem",0)
                    return;
                }
                for(var i = 0;i<this.checked.length;i++){
                    for (var j = 0;j<this.listProduct.length;j++){
                        if (this.checked[i]===this.listProduct[j].name){
                            this.showProduct = this.showProduct.concat(this.listProduct[j])
                            this.products = this.showProduct
                        }
                    }
                }
                this.showProduct = []
                var shouldDel = ""
                if (this.lastItems.length > checked.length){

                    this.lastItems.forEach((item,index)=>{
                        if (checked.indexOf(item)===-1){
                            shouldDel = item
                            this.lastItems.splice(index-1,1)
                        }
                    })
                    this.$store.commit("orderProductItem",shouldDel)
                }
                this.lastItems = checked
            },
            order_add(){
                if(this.$store.state.orderProduct.length===0){
                    Toast.fail("O_o")
                    return
                }
                if (this.customerName===""){
                    Toast.fail("O_o")
                    return
                }
                if (this.destination===""){
                    this.destination = ""
                }
                if (this.remark===""){
                    this.remark = ""
                }
                this.adding_order = true
                axios.get(this.$store.state.domain+'order_add',{
                    params: {
                        remark:this.remark,
                        destination:this.destination,
                        customerName:this.customerName,
                        content:this.$store.state.orderProduct
                    }
                }).then(()=>{
                    router.replace('/')
                    this.adding_order = false
                })
            }
        }
    }
</script>

